<template>
  <div>
    <InputText
      id="txt1"
      type="text"
      v-model="txt1"
      ref="txt1"
      style="width: 200px"
      v-focus="focusArr"
    /><br />
    <InputText
      id="txt2"
      type="text"
      v-model="txt2"
      ref="txt2"
      style="width: 200px"
      v-focus="focusArr"
    /><br />
    <InputText
      id="txt3"
      type="text"
      v-model="txt3"
