      <Button type="button" label="버튼" @click="test1(i)" />
    </template>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
export default {
  components: {},
  data() {
    return {
      sample: '',
      txt1: '',
      txt2: '',
      txt3: '',
      focusArr: ''
    }
  },
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    test1(i) {
      this.focusArr = 'txt' + i
    }
  }
}
</script>

